<script>
// window.onscroll = function() {scrollFunction()};

// function scrollFunction() {
//   if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
//     document.getElementById("navbar").style.top = "0";
//   } else {
//     document.getElementById("navbar").style.top = "-50px";
//   }
// }
import $ from "jquery";

export default {
  name: "myHome",
  data() {
    return {
      cart: [],
      coupon: {},
      // isLoading: false,
      keyword: "",
      category: "",
      active: false,
      stickyTop: 0,
      year: "",
    };
  },
  created() {
    // window.addEventListener("scroll", this.onScroll);
    this.getCart_length();
    this.year = new Date().getFullYear();
  },
  mounted() {
    this.$bus.$on("updateCart", () => {
      this.getCart_length();
    });
    // window.addEventListener("scroll", this.onScroll);
    window.document.onscroll = () => {
      let navBar = document.getElementById("nav");
      if (window.scrollY > navBar.offsetTop) {
        this.active = true;
      } else {
        this.active = false;
      }
    };
  },
  methods: {
    getCart_length() {
      // this.isLoading = true;
      const api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/shopping`;
      this.$http.get(api).then((res) => {
        this.cart = res.data.data;
        //   // this.isLoading = false;
      });
    },
    toProducts() {
      this.$router.push(`/products/${this.category}`);
    },
    //一進來頁面就先抓到category的值；返回上一頁可以抓到this.category的值
    changeCategory(para) {
      // console.log("changeCategory");
      this.category = para;
    },
    search() {
      if (`${this.keyword}` === "") {
        this.$router.push(`/`);
      } else {
        this.$router.push(`/search/${this.keyword}`);
      }
    },
    toggleNavClass() {
      if (this.active == false) {
        return "nav";
      } else {
        return "sticky-nav";
      }
    },
    goTop() {
      $("html,body").animate({ scrollTop: 0 }, 333);
    },
  },
};
</script>

